      document.head.appendChild(script);
    }

    function initMap() {
      var container = document.getElementById("map");
      var options = {
        center: new kakao.maps.LatLng(35.883487491158675, 128.65892377049653),
        level: 5,
      };

      var map = new kakao.maps.Map(container, options);

      for (var index = 0; index < markerInfos.length; index++) {
        var imageSrc = require(`@/assets/${markerInfos[index].img}`);
        // ÎßàÏª§ Ïù¥ÎØ∏ÏßÄÏùò Ïù¥ÎØ∏ÏßÄ ÌÅ¨Í∏∞ ÏûÖÎãàÎã§
        var imageSize = new kakao.maps.Size(
          markerInfos[index].size,
          markerInfos[index].size
        );

        // ÎßàÏª§ Ïù¥ÎØ∏ÏßÄÎ•º ÏÉùÏÑ±Ìï©ÎãàÎã§
        var markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize);

        // ÎßàÏª§Î•º ÏÉùÏÑ±Ìï©ÎãàÎã§
        var marker = new kakao.maps.Marker({
          map: map, // ÎßàÏª§Î•º ÌëúÏãúÌï† ÏßÄÎèÑ
          position: new kakao.maps.LatLng(
            markerInfos[index].lat,
            markerInfos[index].lng
          ), // ÎßàÏª§Î•º ÌëúÏãúÌï† ÏúÑÏπò
          title: markerInfos[index].title, // ÎßàÏª§Ïùò ÌÉÄÏù¥ÌãÄ, ÎßàÏª§Ïóê ÎßàÏö∞Ïä§Î•º Ïò¨Î¶¨Î©¥ ÌÉÄÏù¥ÌãÄÏù¥ ÌëúÏãúÎê©ÎãàÎã§
          image: markerImage, // ÎßàÏª§ Ïù¥ÎØ∏ÏßÄ
        });

        marker.setMap(map);
      }
    }

    // Ïπ¥Ïπ¥Ïò§ Í≥µÏú†
    function shareKakao() {
      window.Kakao.Share.sendDefault({
        objectType: "location",
        address: "ÎåÄÍµ¨ ÎèôÍµ¨ ÎèôÏ¥åÎ°ú 200",
        addressTitle: "ÌÄ∏Î≤®Ìò∏ÌÖî 1Ï∏µ ÌÖåÎùºÌïòÏö∞Ïä§ÌôÄ",
        content: {
          title: "ÍπÄÏ§ÄÏö∞ü§çÏßÑÏÜåÏ†ï Í≤∞ÌòºÏãùÏóê Ï¥àÎåÄÌï©ÎãàÎã§.",
          description:
            "2022ÎÖÑ 09Ïõî 24Ïùº ÌÜ†ÏöîÏùº Ïò§ÌõÑ 1Ïãú ÌÄ∏Î≤®Ìò∏ÌÖî 1Ï∏µ ÌÖåÎùºÌïòÏö∞Ïä§ÌôÄ",
          imageUrl: "@/assets/0416am300203.jpg",
          link: {
            mobileWebUrl: url,
            webUrl: url,
          },
        },
        // social: {
        //   likeCount: 286,
        //   commentCount: 45,
        //   sharedCount: 845,
        // },
        buttons: [
          {
            title: "Ï≤≠Ï≤©Ïû• Î≥¥Í∏∞",
            link: {
              mobileWebUrl: url,
              webUrl: url,
            },
          },
        ],
      });
    }

    // Î¨∏Ïûê Í≥µÏú†ÌïòÍ∏∞

    function checkM(m) {
      location.href =
        "sms:" +
        "" +
        (m == "ios" ? "&" : "?") +
        "body=" +
        encodeURIComponent("ÍπÄÏ§ÄÏö∞ü§çÏßÑÏÜåÏ†ï Í≤∞ÌòºÏãùÏóê Ï¥àÎåÄÌï©ÎãàÎã§.\n" + url);
    }

    function checkMobile() {
      var varUA = navigator.userAgent.toLowerCase(); //userAgent Í∞í ÏñªÍ∏∞
      if (varUA.indexOf("android") > -1) {
        //ÏïàÎìúÎ°úÏù¥Îìú
        return "android";
      } else if (
        varUA.indexOf("iphone") > -1 ||
        varUA.indexOf("ipad") > -1 ||
        varUA.indexOf("ipod") > -1
      ) {
        //IOS
        return "ios";
      } else {
        //ÏïÑÏù¥Ìè∞, ÏïàÎìúÎ°úÏù¥Îìú Ïô∏
        return "other";
      }
    }

    // check mobile and kakao pay
    function sendKakaoPay(url) {
      var isMobile =
        /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
          navigator.userAgent
        );

      if (isMobile) {
        location.href = url;
      } else {
        window.alert("Ïπ¥Ïπ¥Ïò§ÌéòÏù¥Îäî Î™®Î∞îÏùºÏóêÏÑú ÏÇ¨Ïö©Ìï† Ïàò ÏûàÏäµÎãàÎã§.");
      }
    }

    // copy
    function copy(text) {
      copyText(text, undefined, (error, event) => {
        if (error) {
          alert("Can not copy");
          console.log(error);
        } else {
          alert("Î≥µÏÇ¨ÎêòÏóàÏäµÎãàÎã§.");
          console.log(event);
        }
      });
    }

    // d-day Í≥ÑÏÇ∞
    function getDday() {
      var today = new Date();
      var day = new Date(2022, 9, 24);
      var gap = day.getTime() - today.getTime();
      var result = Math.ceil(gap / (1000 * 60 * 60 * 24));
      return result;
    }

    // const attrs = {
